.modal{
  position: fixed;
  height: 355px;
  z-index: 2000;
  background-color: rgb(73, 4, 36);
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  top: 220px;
  margin: 50px 254px auto;
  max-width: 70%;
  min-width: 450px;
  border-radius: 10px;
  width: 61%;  
}
    
.fechar{
  border-radius: unset;
  color: #fff;
  font-weight: 700;
  margin-top: 0;
  min-width: unset;
  padding: 5px 0;
  position: absolute;
  right: 0;
  top: 0;
  width: 40px;
}
    
.textmodal { 
  text-align: center;
  font-style: italic;
  font-weight: 600; 
}
    
.modal input{
  margin-bottom: 15px;
  min-width: 273px;
  width: 80%;
  padding: 12px;
  border-radius: 5px;  
}
    
    
.modal select {
  cursor: pointer;
  font-family: var(--font_primary_family);
  font-weight: 800;
  margin-bottom: 25px;
  max-width: 331px;
  min-width: 300px;
  width: 0.7%;
  padding: 5px;
  border-radius: 5px;
}
    
.modal button{
  margin-bottom: 15px;
  min-width: 100px;
  color: rgb(14, 7, 7);
  width: 86%;
  padding: 12px;
  border-radius: 5px;
}
    
.texmodal {
  color: #c2ed92;
}
    
.botaomodal {
  border: none;
  border-radius: 5px;
  color: #fff;
  cursor: pointer;
  font-size: 1em;
  font-weight: 700;
  margin: 0px 0 30px;
  min-width: 350px;
  padding: 0px;
  transition: -webkit-filter .2s;
  transition: filter .2s;
  transition: filter .2s, -webkit-filter .2s;
  transition: filter .2s,-webkit-filter .2s;
  width: 50%;
}
    
.p{
  line-height: 23.5px;
  margin: unset;
  margin-left: 0px;
}
  
    
  @media screen and (max-width: 500px) {  
    
    .modal {
      justify-content: unset;
      max-width: unset;
      min-width: 450px;
      width: 100%;
      align-items: center;
      background-color: rgb(193, 189, 198);
      display: flex;
      flex-direction: column;
      margin: 80px auto auto; 
    }
    
}
section{
  margin-top: 0px;
}



body {
  background-color: #2e3047;
  background-color: #2e3047;
  background-color: var(--background_color2);
  color: #fff;
  color: #fff;
  color: var(--primary_color);
  font-family: Roboto,sans-serif;
  font-family: "Roboto",sans-serif;
  font-family: var(--font_primary_family);
  font-size: 1.1em;
}
body {
  line-height: 2;
}

:root {
  --background_color1: #373957;
  --background_color2: #2e3047;
  --background_color3: #27283a;
  --primary_color: #fff;
  --secundary_color: #000;
  --font_primary_family: "Roboto",sans-serif;
  --user_font_family: "Poppins",sans-serif;
  --user_color: #d1da59;
}



div {
  border: 0;
  box-sizing: border-box;
  font-size: 100%;
  font: inherit;
  margin: 0;
  padding: 0;
  vertical-align: baseline;
}

.listatodos{
  border: 0;
  box-sizing: border-box;
  font-size: 100%;
  font: inherit;
  margin: 0;
  padding: 0;
  vertical-align: baseline;
  align-items: center;
  background-image: linear-gradient(0deg,#373957,#2e3047,#27283a);
  border-bottom: 0.1px solid #fff;
  box-shadow: inset 0 0 100px 30px #27283a;
  box-shadow: inset 0 0 100px 30px var(--background_color3);
  display: grid;
  grid-template-rows: auto;
  margin: auto;
  width: 100%;
}

.image {
  padding-left: 90px;
  
}

#imagem {
  border-radius: 50%;

}

.dados {
  margin: 25px 0;
  padding-left: 35px;
}
.nome ,p{
  line-height: 23.5px;
  margin: unset;
  margin-left: 0px;
}

.user ,p{
  margin: unset;
  color: #c2ed92;
  margin-left: 0px;
  line-height: 23.5px;
}


.container{
  align-items: center;
  background-image: linear-gradient(0deg,#171a3d,#0d114e,#0b0f5f);
  border-bottom: 0.1px solid #fff;
  box-shadow: inset 0 0 100px 30px #27283a;
  box-shadow: inset 0 0 100px 30px var(--background_color3);
  display: grid;
  grid-template-columns: .1fr 1.8fr .7fr;
  grid-template-rows: auto;
  margin: auto;
  width: 100%;
}

.botao{
  text-align: center;
  
}

#botao1 {
  border: unset;
  border-radius: 5px;
  box-shadow: 8px 8px 8px rgba(57, 65, 33, 0.932);
  cursor: pointer;
  font-size: 1em;
  font-weight: 600;
  height: 29px;
  text-align: center;
  width: 73px;
}


@media screen and (max-width: 500px) {
 
  .container{
    border: solid #27283a;
    border: solid var(--background_color3);
    border-radius: 10px;
    box-shadow: inset 0 0 100px 30px #27283a;
    box-shadow: inset 0 0 100px 30px var(--background_color3);
    grid-template-columns: .8fr;
    justify-content: center;
    margin-bottom: 20px;
    max-width: 85%;
    min-width: 421px;
    width: 80%;
  }



  div {
    display: block;
  }

  body {
    background-color: #2e3047;
    background-color: #2e3047;
    background-color: var(--background_color2);
    color: #fff;
    color: #fff;
    color: var(--primary_color);
    font-family: Roboto,sans-serif;
    font-family: "Roboto",sans-serif;
    font-family: var(--font_primary_family);
    font-size: 1.1em;
  }
  body {
    line-height: 1;
  }
  :root {
    --background_color1: #373957;
    --background_color2: #2e3047;
    --background_color3: #27283a;
    --primary_color: #fff;
    --secundary_color: #000;
    --font_primary_family: "Roboto",sans-serif;
    --user_font_family: "Poppins",sans-serif;
    --user_color: #d1da59;
  }

  #botao1 {
     width: 100%;
  }

  #botao1 {
    border: unset;
    border-radius: 5px;
    box-shadow: 2px 2px 4px rgb(0 0 0 / 15%);
    cursor: pointer;
    font-size: 1em;
    font-weight: 600;
    height: 35px;
    text-align: center;     
  }

  .dados {
    margin: 25px 0;
    padding-left: 0px;
  }
}

@media screen and (max-width: 500px) {
  .container:after {
    display: none;
  }
}

